@import url("https://fonts.googleapis.com/css2?family=Unica+One&family=Urbanist:wght@400;600&display=swap");

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Unica One", sans-serif;
  // font-family: "Urbanist";
  background: #fff;
  font-size: 20px;
  line-height: 1.1;
  color: #000;
}

.app {
  max-width: 1340px;
  padding: 0 20px;
  margin: 0 auto;
}

img {
  width: 100%;
}

h1,
h2,
h3,
h4 {
  margin: 0;
  text-transform: uppercase;
}

h1 {
  text-align: center;
  margin: 20px 0 50px;
  font-size: 48px;
}

h2 {
  font-size: 44px;
}

h3 {
  font-size: 38px;
  font-weight: 400;
}

p {
  margin: 0;
}

a {
  display: inline-block;
  text-decoration: none;
}

ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  cursor: pointer;
  font-family: inherit;
  line-height: inherit;
}

.btn {
  background: #353535;
  border: none;
  box-shadow: none;
  color: #fff;
  padding: 10px 35px;
  font-size: 28px;
  text-transform: uppercase;
  &--add-cart {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    span {
      width: 24px;
      height: 24px;
      background: url(./assets/icons/plus.svg);
    }
  }
}

.wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 30px;
}
