.filter {
  font-family: "Urbanist";
  display: flex;
  gap: 10px;
}

.filter__item {
  border: 1px solid transparent;
  padding: 8px 20px;
  cursor: pointer;
  position: relative;
  &::after {
    content: "";
    position: absolute;
    width: 0;
    height: 1px;
    bottom: 0;
    left: 20px;
    background: #000;
    transition: 0.15s;
  }
  &:hover {
    &::after {
      width: calc(100% - 40px);
    }
  }
  &.active {
    border: 1px solid #000;
    &::after {
      content: none;
    }
  }
}
