.header {
  padding: 45px 0;
}

.header__search {
  border-bottom: 1px solid #e8e8e8;
  max-width: 400px;
  width: 100%;
  position: relative;
  padding: 0 0 5px 30px;
  transition: 0.1s;
  &::before {
    content: "";
    position: absolute;
    background: url(../../assets/icons/search.svg);
    left: 0;
    top: 2px;
    width: 24px;
    height: 24px;
  }
  &:hover,
  &:has(.header__search-input:focus) {
    border-bottom: 1px solid #000;
  }
}

.header__search-input {
  border: none;
  box-shadow: none;
  outline: none;
  width: 100%;
  font-size: 20px;
  font-family: "Urbanist";
  &::placeholder {
    color: #bfbfbf;
  }
}

.header__cart {
  position: relative;
  &::before {
    content: "";
    position: absolute;
    right: 0;
    top: -12px;
    width: 24px;
    height: 24px;
    background: url(../../assets/icons/cart.svg);
  }
}
