.sort {
  font-family: "Urbanist";
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 215px;
  &.active {
    .sort__current {
      &::after {
        transform: rotateX(180deg) translateY(-5%);
      }
    }
    .sort__list {
      opacity: 1;
      visibility: visible;
    }
  }
}

.sort__current {
  position: relative;
  padding-right: 30px;
  cursor: pointer;
  &::after {
    content: "";
    position: absolute;
    width: 24px;
    height: 24px;
    right: 0;
    top: -1px;
    transition: 0.1s;
    background: url(../../assets/icons/arrow-down.svg);
  }
}

.sort__list {
  position: absolute;
  top: 35px;
  cursor: pointer;
  min-width: 100%;
  border-radius: 4px;
  background: #fff;
  box-shadow: 0px 4px 10px 0px rgba(53, 53, 53, 0.2);
  overflow: hidden;
  opacity: 0;
  visibility: hidden;
  transition: 0.1s;
}

.sort__item {
  padding: 10px 15px;
  &:hover {
    font-weight: 600;
  }
  &.active {
    font-weight: 400;
    background: #353535;
    color: #fff;
  }
}
